fetch('https://openexchangerates.org/api/latest.json?app_id=e1a03227b2e444538bd8ed483a35da7b').then((resp) => {
  let json = resp.json(); // there's always a body
  if (resp.status >= 200 && resp.status < 300) {
    console.log(json);
  } else {
    return json.then(Promise.reject.bind(Promise));
  }
})


